<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>全局搜索</h5>
      </div>

      <div class="ibox-content">

        <div class="row">
          <form @submit.prevent="search">
            <div class="searchOption">
              <label class="control-label">关键字</label>
              <input type="text" class="form-control" v-model="condition.key">
            </div>
            <button type="submit" class="btn btn-warning search">查询</button>
          </form>
        </div>


        <table class="table table-bordered text-center">
          <thead>
            <tr>
              <th>所属模块</th>
              <th>标题</th>
              <th>关键字</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item of items">
              <td>{{ item.case | codeName }}</td>
              <td>{{ item.name }}</td>
              <td>{{ item.key }}</td>
              <td>
                <button class="btn btn-sm btn-primary" @click="details(item)">详情</button>
                <button class="btn btn-sm btn-info" @click="details(item, condition.key)">查看列表</button>
              </td>
            </tr>
          </tbody>
        </table>
        <vue-pagination :current-page="page" :total="total" :page-size="pageSize" @current-change="pageChange" theme="gray"></vue-pagination>

      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import { PAGE_SIZE } from '@/assets/config'
import { CODE } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import Service from '@/assets/service/service'
export default {
  name: 'register',
  data () {
    return {
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      condition: {},
      items: []
    }
  },
  mounted () {

  },
  methods: {
    details (item, key) {
      let url, name
      switch (item.case) {
        // 基本信息
        case CODE.Researcher:
          url = 'collegePersonnel/scientificResearchPersonnel'
          name = '科研人员'
          break
        case CODE.TalentPlan:
          url = 'collegePersonnel/talentPlan'
          name = '人才计划'
          break
        case CODE.AcademicCommittee:
          url = 'collegePersonnel/academicCommittee'
          name = '学术委员会'
          break
        case CODE.EthicsCommittee:
          url = 'collegePersonnel/ethicsCommittee'
          name = '伦理委员会'
          break
        case CODE.CollegeArea:
          url = 'collegeFacilities/area'
          name = '实验场地'
          break
        case CODE.CollegeDevice:
          url = 'collegeFacilities/device'
          name = '研究设备'
          break
        // 学术成果
        case CODE.AcademicWorks:
          url = 'academicWorks'
          name = '学术著作'
          break
        case CODE.PeriodicalPaperChinese:
          url = 'periodicalPaper/periodicalPaperChinese'
          name = '期刊论文-中文'
          break
        case CODE.PeriodicalPaperEnglish:
          url = 'periodicalPaper/periodicalPaperEnglish'
          name = '期刊论文-英文'
          break
        case CODE.ResearchProjectsCountry:
          url = 'researchProjects/country'
          name = '科研项目(国家级项目)'
          break
        case CODE.ResearchProjectsProvince:
          url = 'researchProjects/province'
          name = '科研项目(省部级项目)'
          break
        case CODE.ResearchProjectsOther:
          url = 'researchProjects/other'
          name = '科研项目(其他纵向项目)'
          break
        case CODE.ResearchProjectsLandscape:
          url = 'researchProjects/landscape'
          name = '科研项目(横向项目)'
          break
        case CODE.ResearchAwards:
          url = 'researchAwards'
          name = '科研获奖'
          break
        case CODE.Patent:
          url = 'patent'
          name = '发明专利'
          break
        case CODE.MeetingPaper:
          url = 'meetingPaper'
          name = '会议论文'
          break
        case CODE.ResearchReport:
          url = 'researchReport'
          name = '研究报告'
          break
        case CODE.HostMeeting:
          url = 'hostMeeting'
          name = '主办会议/培训'
          break
        case CODE.ParticipateMeeting:
          url = 'participateMeeting'
          name = '参与会议/培训'
          break
        case CODE.Score:
          url = 'score'
          name = '评分'
          break
        // 人才管理
        case CODE.MasterTraining:
          url = 'masterTraining'
          name = '硕士培养'
          break
        case CODE.DoctorTraining:
          url = 'doctorTraining'
          name = '博士培养'
          break
        case CODE.TeacherAward:
          url = 'teacherAward'
          name = '教学获奖'
          break
        case CODE.GoodCourse:
          url = 'goodCourse'
          name = '精品课程'
          break
        case CODE.TeachingMaterialConstruction:
          url = 'teachingMaterialConstruction'
          name = '教材建设'
          break
        case CODE.StudentAwards:
          url = 'studentAwards'
          name = '学生获奖情况'
          break
        case CODE.StudentWorks:
          url = 'studentWorks'
          name = '学生发表著作情况'
          break
        case CODE.StudentMeeting:
          url = 'studentMeeting'
          name = '学生境内/境外参加会议'
          break
        case CODE.StudentStudy:
          url = 'studentStudy'
          name = '学生境内/境外交流学习情况'
          break
        case CODE.StudentMeetingPaper:
          url = 'studentMeetingPaper'
          name = '学生发表会议论文'
          break
        case CODE.PracticeBase:
          url = 'practiceBase'
          name = '实践基地'
          break
        case CODE.PartTimeTutor:
          url = 'partTimeTutor'
          name = '兼职导师'
          break
        case CODE.ParticipateProfessionalPractice:
          url = 'participateProfessionalPractice'
          name = '参加专业实践活动'
          break
        case CODE.GraduateEnrollment:
          url = 'graduateEnrollment'
          name = '招生与毕业情况'
          break
        case CODE.RulesRegulations:
          url = 'rules'
          name = '规则制度'
          break
        default:
          alert('请配置url')
          return
          break;
      }
      if (key) {
        tabsService.addTab('/views/' + url + '?key=' + key, url, '全局搜索-' + name)
      } else {
        tabsService.addDetailsTab('/views/' + url + '/' + item.id, name + '-详情-' + item.id)
      }
    },
    getDataTables(page = 1) {
      this.condition['page'] = page
      if (!this.condition.key) {
        toastr.warning('请输入搜索关键字')
        return
      }
      Service.Search.get(this.condition).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
        }
      })
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    search () {
      this.getDataTables()
    }
  }
}
</script>


<style scoped>
  .searchOption {
    display: inline-block;
    float: left;
    margin-bottom: 10px;
  }
  .searchOption label {
    display: inline-block;
    float: left;
    margin: 8px 15px 0 15px;
  }
  .searchOption input {
    display: inline-block;
    float: left;
    width: 150px;
  }
  .search {
    margin-left: 10px;
  }
</style>



